"use client";
import { useEffect } from "react";
import axios from "axios";

export default function Page() {
    const test = async () => {
        const res = await axios.get("http://localhost:3333/testCookie", {
            withCredentials: true,
        });

        console.log(res.data);
    };
    useEffect(() => {
        test();
    }, []);
    return <div>testing cookie</div>;
}
